@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

@media screen and ( max-width: 770px ) {
    body {
        font-family: 'Inter', sans-serif;
        color: #333333;
    }

    .glass {
        width: 100%;
        height: 80vh;
        vertical-align: middle;
        background: linear-gradient(111deg, rgba(255, 255, 255, 0.09) 0%, rgba(0, 0, 0, 0.09) 100%);
        backdrop-filter: blur(10px);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        padding: 10px;
    }

    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .header .logo {
        font-size: 18px;
        text-decoration: none;
        margin-left: 10px;
        width: 10vw;
    }

    .nav-items {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-right: 10px;
    }

    .nav-items a {
        text-decoration: none;
        color: #333333;
    }

    .intro {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 52vh;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        margin-top: 5vh;
    }

    .intro h1 {
        font-family: sans-serif;
        font-size: 64px;
        font-weight: bold;
        text-transform: uppercase;
        margin: 0;
    }

    .intro p {
        /*font-size: 20px;*/
        text-transform: uppercase;
        margin: 20px 0;
    }

    .btn {
        text-decoration: none;
        border: 1px solid #333333;
        position: relative;
        overflow: hidden;
        background: rgba(127, 127, 127, 0);
        outline: none;
    }

    .btn:hover {
        box-shadow: 1px 1px 25px 10px #FFFFFF;
        border: 1px solid #FFFFFF;
    }

    .btn:before {
        content: "";
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(
                120deg,
                transparent,
                #FCA903,
                transparent
        );
        transition: all 650ms;
    }

    .btn:hover:before {
        left: 100%;
    }

    .a_btn {
        margin-right: 5px;
        text-decoration: none;
        color: #333333;
        background: rgba(127, 127, 127, 0);
        outline: none;
        border: 1px solid rgba(127, 127, 127, 0);
        transition: color 1s;
    }

    .a_btn:hover {
        text-decoration: none;
        color: #FFFFFF;
    }

    .a_btn:active {
        outline: none;
    }

    .a_btn:focus {
        outline: none;
    }

    .background {
        position: fixed;
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
        margin: 0;
        padding: 0;
        background: #fca903;
        overflow: hidden;
    }

    .background li {
        position: absolute;
        display: block;
        list-style: none;
        width: 20px;
        height: 20px;
        background: rgba(255, 255, 255, 0.2);
        animation: animate 19s linear infinite;
    }

    .background li:nth-child(0) {
        left: 71%;
        width: 283px;
        height: 283px;
        bottom: -283px;
        animation-delay: 1s;
    }

    .background li:nth-child(1) {
        left: 76%;
        width: 332px;
        height: 332px;
        bottom: -332px;
        animation-delay: 1s;
    }

    .background li:nth-child(2) {
        left: 45%;
        width: 145px;
        height: 145px;
        bottom: -145px;
        animation-delay: 9s;
    }

    .background li:nth-child(3) {
        left: 10%;
        width: 364px;
        height: 364px;
        bottom: -364px;
        animation-delay: 5s;
    }

    .background li:nth-child(4) {
        left: 57%;
        width: 71px;
        height: 71px;
        bottom: -71px;
        animation-delay: 8s;
    }

    .background li:nth-child(5) {
        left: 42%;
        width: 218px;
        height: 218px;
        bottom: -218px;
        animation-delay: 25s;
    }

    .background li:nth-child(6) {
        left: 16%;
        width: 126px;
        height: 126px;
        bottom: -126px;
        animation-delay: 3s;
    }

    .background li:nth-child(7) {
        left: 71%;
        width: 220px;
        height: 220px;
        bottom: -220px;
        animation-delay: 30s;
    }

    .background li:nth-child(8) {
        left: 36%;
        width: 316px;
        height: 316px;
        bottom: -316px;
        animation-delay: 2s;
    }

    .background li:nth-child(9) {
        left: 35%;
        width: 301px;
        height: 301px;
        bottom: -301px;
        animation-delay: 32s;
    }
}
