@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');


@media screen and ( max-width: 770px ) {
    body {
        font-family: 'Inter', sans-serif;
        color: #333333;
    }

    .rotation {
        margin: auto;
        text-align: right;
    }

    .quote {
        text-align: right;
    }

    .arrows {
        margin-left: 10%;
    }

    .img_ap {
        width: 0vh;
    }

    .info-container {
        width: 0%;
        background: rgba(127, 127, 127, 0);
        display: flex;
        justify-content: center;
        align-items: center;
    }
    /*Левая часть*/
    .image-container {
        width: 100%;
        height: 100%;
        background: rgba(127, 127, 127, 0);
        overflow: hidden;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .label_parent {
        display: inline;
    }

    .label_parent a {
        display: inline;
    }

    .label_parent a:hover {
        display: inline;
        color: #FFFFFF;
    }

    .label_new {
        padding: 10px;
        border-radius: 5px;
        cursor: pointer;
    }
}

@media screen and ( min-width: 770px ) {
    body {
        font-family: 'Inter', sans-serif;
        color: #333333;
    }

    .rotation {
        margin: auto;
        text-align: right;
        margin-right: 10%;
    }

    .quote {
        text-align: right;
    }

    .arrows {
        margin-left: 10%;
    }

    .img_ap {
        width: 30vw;
        margin-left: 9vw;
    }

    .label_parent {
        display: inline;
    }

    .label_parent a {
        display: inline;
    }

    .label_parent a:hover {
        display: inline;
        color: #FFFFFF;
    }

    .label_new {
        padding: 10px;
        border-radius: 5px;
        cursor: pointer;
    }

    .glass_0 {
        width: 100%;
        height: 80vh;
        vertical-align: middle;
        background: linear-gradient(111deg, rgba(255, 255, 255, 0.09) 0%, rgba(0, 0, 0, 0.09) 100%);
        backdrop-filter: blur(10px);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        padding: 20px;
    }
}